import { _decorator, CCFloat, Component, Node, Collider2D, Contact2DType, CCString, find } from 'cc';
import { Manager } from './Manager';
import { ManagerGeneral } from './ManagerGeneral';
import { Lanzame } from './Lanzame';
const { ccclass, property } = _decorator;

@ccclass('Objetos')
export class Objetos extends Component {

    @property(CCFloat) valor: number=10;
    @property(CCString) tipo: string="";
    @property(Manager)manager:Manager=null;
    @property(ManagerGeneral)managerG:ManagerGeneral=null;
    a:Node=null;
    @property(Node) player:Node=null;
    tiempoRestante:number=10;
    onLoad()
    {
        this.a=find("Canvas/ManagerGeneral");
        this.managerG=this.a.getComponent(ManagerGeneral);
        
        ////COLLIDER////
        let collider = this.getComponent(Collider2D);
        if (collider) 
        {
            // Called only once when two colliding bodies start to make contact
            collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
            //collider.on(Contact2DType.END_CONTACT, this.onEndContact, this);
 
        }
    }

    start() {

    }

    update(dt) 
    {
        if(this.player.getComponent(Lanzame).iman&&this.tipo=="moneda")
            {
                this.Iman(dt);
                
                let newPosition = this.node.worldPosition.lerp(this.player.getWorldPosition(), 2 * dt);
               
                this.node.worldPosition = newPosition;
                
                
            }
    }

    Iman(dt)
    {
        if (this.tiempoRestante > 0) {
            this.tiempoRestante-=dt; // Reduce el tiempo restante
           
        } 
        else 
        {
            this.player.getComponent(Lanzame).iman=false;
            this.managerG.iman=0;
            this.managerG.Save();
        }
    }

    onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D)
    {
        if(otherCollider.group===4)
        {
            
            switch(this.tipo)
            {
                
                case 'tarjeta':
                    this.manager.moneda=false;
                    this.manager.contadorTarjetas++;
                   
                    this.manager.score+=this.valor;
                    
                    this.manager.ActualizaScore(false);
                    break;
                case 'moneda':
                    this.manager.moneda=true;
                    this.manager.contadorMonedas++;
                   
                    this.manager.score+=this.valor;
                    
                    this.manager.ActualizaScore(true);
                    break;
            }

            

            this.scheduleOnce(function()
            {
                this.node.destroy();
            },1)
        }
    }
}


