import { _decorator, Component, Node, randomRangeInt, instantiate, v3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Tarjetas')
export class Tarjetas extends Component {

    @property ([Node]) posiciones:Node []=[];
    @property (Node) tarjeta:Node=null;
    @property(Node) temp:Node=null;
    @property(Node) camino:Node=null;

    onLoad()
    {
      
        this.tarjeta.active=false;
    }
    start() {

    }

    update(deltaTime: number) {
        
    }

    Donde(cam:boolean)
    {
        if(cam)
            {
                this.Mostrar();
            }

    }

    Mostrar()
    {
        let a=randomRangeInt(0,2);

        switch(a)
        {
            case 0:
                this.temp=instantiate(this.tarjeta);
                this.temp.parent=this.posiciones[0];
                this.temp.position=v3(0,0,0);
                this.temp.active=true;
                break;
            case 1:
                this.temp=instantiate(this.tarjeta);
                this.temp.parent=this.posiciones[1];
                this.temp.position=v3(0,0,0);
                this.temp.active=true;
                break;
            case 2:
                /*this.temp=instantiate(this.tarjeta);
                this.temp.parent=this.posiciones[2];
                this.temp.position=v3(0,0,0);
                this.temp.active=true;*/
                console.log("No hay tarjeta");
                break;
            case 3:
                console.log("No hay tarjeta");
                break;
        }
    }
}


